body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App{
    width: 100%;
    height: 100vh;
    background-image: linear-gradient(-45deg,#6D1221, #09316E);
    box-sizing: border-box;
    overflow-y: hidden;
}
.App>.wrap{
    display: -webkit-flex;
    display: flex;
    height: 100vh;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-direction: column;
            flex-direction: column;
}
.home_wrap {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.show{
    display: block !important;
}
.hide {
    display: none !important;
}
.App>.wrap .date_time{
    display: block;
    position: relative;
    margin-bottom: 10px;
}
.App>.wrap .date_time>span.time{
    float: right;
    color: #FFF;
    font-size: 14px;
    margin: -8px 0;
}
.App>.wrap .date_time>span.day{
    display: block;
    color: rgba(172,172,172,10);
    font-size: 50px;
}
.App>.wrap .date_time>span.date{
    float: right;
    display: block;
    color: rgba(172,172,172,.35);
    font-size: 18px;
}
.App>.wrap .location{
    display: block;
    font-size: 36px;
    color: rgba(172,172,172,50);
}
.App>.wrap .social_widget{
    display: grid;
    width: 400px;
    height: 290px;
    margin-top: 30px;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr 1fr;
    grid-template-areas: "Facebook Facebook Google Google Google" "Facebook Facebook Linkedin Youtube Youtube" "SnapChat Spotify Spotify Youtube Youtube" "SnapChat Vine Amazon Amazon twitch";
}
.App>.wrap .social_widget .items {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}
.App>.wrap .social_widget>.Facebook {
    grid-area: Facebook;
    background: #3B5999;
}
.App>.wrap .social_widget>.Facebook>img, .App>.wrap .social_widget>.Youtube>img {
    width: 40px;
    height: 40px;
}

.App>.wrap .social_widget>.Google {
    grid-area: Google;
    background: #4285F4;
}
.App>.wrap .social_widget>.Google>img, .App>.wrap .social_widget>.SnapChat>img, .App>.wrap .social_widget>.Spotify>img {
    height: 32px;
    width: 32px;
}
.App>.wrap .social_widget>.Linkedin {
    grid-area: Linkedin;
    background: #0077B5;
}

.App>.wrap .social_widget>.Youtube {
    grid-area: Youtube;
    background: #FF0000;
}

.App>.wrap .social_widget>.SnapChat {
    grid-area: SnapChat;
    background: #FFFC00;
}

.App>.wrap .social_widget>.Spotify {
    grid-area: Spotify;
    background: #1ED760;
}

.App>.wrap .social_widget>.Vine {
    grid-area: Vine;
    background: #1AB7EA;
}
.App>.wrap .social_widget>.Vine>img, .App>.wrap .social_widget>.twitch>img, .App>.wrap .social_widget>.Linkedin>img {
    width: 32px;
    height: 32px;
}

.App>.wrap .social_widget>.Amazon {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    grid-template-areas: "Amazone Amazone Amazone" "Quora Quora Medium";
    grid-area: Amazon;
}

.App>.wrap .social_widget>.Amazon>.Amazone {
    grid-area: Amazone;
    background: #FF9900;
}
.App>.wrap .social_widget>.Amazon>.Amazone>img {
    width: 24px;
    height: 24px;
}

.App>.wrap .social_widget>.Amazon>.Quora {
    grid-area: Quora;
    background: #AA2200;
}
.App>.wrap .social_widget>.Amazon>.Quora>img, .App>.wrap .social_widget>.Amazon>.Medium>img {
    width: 24px;
    height: 24px;
}

.App>.wrap .social_widget>.Amazon>.Medium {
    grid-area: Medium;
    background: #02B875;
}

.App>.wrap .social_widget>.twitch {
    grid-area: twitch;
    background: #6441A4;
}

.App>.wrap .left_side_widget {
    float: left;
    position: absolute;
    left: 0;
}

.App>.wrap .left_side_widget>.toogle_wrap {
    width: 65px;
    background: rgba(0,0,0, .31);
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
}
.App>.wrap .left_side_widget>.expend {
    display: none;
    width: 55px;
    background: rgba(0,0,0, .31);
    cursor: pointer;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
}
.App>.wrap .left_side_widget>.expend img{
    padding: 10px 8px;
    width: 35px;
    height: 35px;
}
.toogle_wrap li.toogle_icon {
    background-color: rgba(3, 21, 59, .62);
    padding: 5px;
    -webkit-align-items: center;
            align-items: center;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
}
.toogle_wrap li {
    padding: 8px 0;
    margin: 0;
    list-style: none;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}
.App>.wrap .left_side_widget>.toogle_wrap:hover {
    cursor: pointer;
}
.toogle_wrap li.more {
    padding-bottom: 16px;
}
.toogle_wrap a>li img {
    width: 24px;
    height: 24px;
}
.toogle_wrap li.more img {
    width: 24px;
    height: 8px;
}
.toogle_wrap li.toogle_icon img{
    width: 24px;
    height: 12px;
}
.footer{
    display: none;
  width: 100%;
  background-image: linear-gradient(100deg, rgba(8,20,107, .38), rgba(39,3,12,.23));
  z-index: 10;
  position: absolute;
  bottom: 0;
    opacity: 0;
}
.footer>.wrap {
    display: -webkit-flex;
    display: flex;
    height: 80px;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    padding: 0 85px;
}
.footer>.wrap>li{
  -webkit-flex: 1 1;
          flex: 1 1;
  height: auto;
  list-style: none;
  padding: 0;
  margin: 0;
}
.footer>.wrap>li>.credit {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  color: #ffffff;
}
.footer>.wrap>li>.credit>span{
  color: rgba(172,172,172,48);
  font-size: 18px;
  margin-right: 20px;
}
.footer>.wrap>li>.credit a{
  text-decoration: none;
  display: block;
  margin-left: 10px;
}
.footer>.wrap>li>.credit>img{
  float: left;
  height: 50px;
  width: 50px;
  border-radius: 50%;
}
.footer>.wrap>li>.credit>a>h2.name{
  -webkit-flex: 1 1;
          flex: 1 1;
  margin: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: start;
          align-items: start;
  width: 100%;
  color: rgba(172,172,172,48);
  font-size: 22px;
}
.footer>.wrap>li>.credit>a>.title{
  color: rgba(172,172,172,48);
  font-size: 11px;
  display: block;
}
.footer>.wrap>li>.utilities{
  text-align: center;
  display:block;
  float: left;
  padding: 0 40px;
}
.footer>.wrap>li>.utilities .icon{
  color: rgba(172,172,172,48);
  font-size: 32px;
  margin: 0 !important;
}
.footer>.wrap>li>.utilities span{
  display:block;
  color: rgba(172,172,172,48);
  margin-top: 8px;

}
.footer>.wrap>li>.share{
  float: right;
  color: #EFEBEB;
    cursor: pointer;
}
.footer>.wrap>li>.share img{
  width: 40px;
  height: 40px;
}


